.faqContent {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    align-content: center;
    width: 100vw;
    overflow: hidden;
    height: 100vh;
}

.faqTitle {
    font-size: clamp(2rem, 5vw, 5rem);
}

.faqSections {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.faqSection {
    color: black;
    font-size: clamp(1rem, 2vw, 2.5rem);
    margin: .5em;
    font-family: Cormorant;
    cursor: pointer;
    font-weight: 300;
    transition: all 0.5s ease;
}

.faqSelectedSection {
    color: #daaa98;
    font-weight: 500;
    border-bottom: solid 2px #daaa98;
}

.faqItems {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100vw;
    justify-content: space-evenly;
}

.faqItem {
    width: 25vw;
    border-radius: 32px;
    border: 2px solid #daaa98;
    padding: 16px;
    margin: 16px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.faqItemTitle {
    font-size: clamp(1rem, 2.5vw, 1.8rem);
    font-weight: 200;
}

.faqItemDescription {
    font-size: clamp(1rem, 1.3vw, 6rem);
    font-family: Cormorant;
    font-weight: 200;
    color: black;
}

@media(max-width: 768px) {
    .faqTitle {
        font-size: clamp(1rem, 10vw, 9rem);
    }

    .faqSection {
        font-size: clamp(1rem, 6vw, 2.5rem);
    }

    .faqMobileItems {
        width: 100%;
    }

    .faqItemWrapper {
        width: 100%;
        display: flex;
        margin: 0;
        justify-content: center;
    }

    .faqItem {
        width: 80vw;
        margin: 16px;
    }

    .faqItemTitle {
        font-size: clamp(1rem, 6vw, 2.5rem);
    }

    .faqItemDescription {
        font-size: clamp(1rem, 5vw, 2.5rem);
    }

    .arrow {
        border: solid black;
        border-width: 0 3px 3px 0;
        position: absolute;
        padding: 3px;
    }

    .right {
        transform: rotate(-45deg);
        right: 0;
        text-align: center;
        -webkit-transform: rotate(-45deg);
    }

    .left {
        transform: rotate(135deg);
        -webkit-transform: rotate(135deg);
    }
}